import "./CourseCard.css";
import { useState } from "react";
function randomKey() {
  return Math.floor(Math.random()*Date.now());
}
const CourseCard = () => {
  const [courseList, setCourseList] = useState([
    {
      title: "HTML & Css",
      text: "Html and Css is xml type markup languages introduce for web",
      udemy: ["Deveslop fullstack course"],
      youtube: ["Prof.Steve", "Brad Traversery", "OnlineTutrial Css Expert"],
      web: ["MDN (Mozila Developer Network ) docs", "W3schools"],
    },
    {
      title: "Javascrip",
      text: "Javascript is run inside Broswer and Nodejs",
      udemy: ["Ninja Mastering javascript"],
      youtube: ["Prof.steve", "Brad Traversery", "Joiner Developer Center"],
      web: ["MDN (Mozila Developer Network ) docs", "W3schools"],
    },
    {
      title: "Reactjs",
      text: "Reactjs is modern virtual dom ui library",
      udemy: ["Jhon Simgla"],
      youtube: [
        "Jhon Simgla Youtube channel",
        "Prof.Steve",
        "Brad Traversery",
        "Joiner Developer Center",
      ],
      web: [
        "Reactjs docs",
        "MDN (Mozila Developer Network ) docs",
        "W3schools",
      ],
    },
    {
      title: "Nodejs & Expressjs & Mongodb",
      text: "Nodejs is Asyncrous event loop basied bekend server.",
      udemy: ["Jhonass cheotman nodejs Bootcamp"],
      youtube: [
        "Jhon Simgla Youtube channel",
        "Prof.Steve",
        "Brad Traversery",
        "Joiner Developer Center",
        "Ninja Nodejs Course",
      ],
      web: [
        "Nodejs docs docs",
        "MDN (Mozila Developer Network ) docs",
        "W3schools",
      ],
    },
    {
      title: "Flutter & Dart",
      text: "Flutter is crossplatform ui sdk from Google.",
      udemy: [],
      youtube: [
        "Flutterly Dart lang course",
        "Fullstack school Flutter course",
        "Ninaj Flutter course",
      ],
      web: ["Flutter docs", "Flutter Api refrence"],
    },
  ]);
  return (
    <div className="courseCard p-4">
      {courseList.map((item,index) => {
        return <InnerCard key={item.title+index} {...item} />;
      })}
    </div>
  );
};
const InnerCard = ({ title, text, udemy, youtube, web }) => {
  return (
    <div className="card p-3">
      <div className="titleBox">
        <h3>{title}</h3>
        <p className="text-secondary">{text}</p>
      </div>

      <div className="row subtitle">
        <div className="col-1 mr-3  icon">
          <svg
            fill="brown"
            opacity="1.0"
            baseProfile="full"
            width="24"
            height="24"
            viewBox="0 0 24.00 24.00"
          >
            <path d="M2 5v13h9v-2H4V7h13v4h2V5H2zm7 4v5l3.5-2.5L9 9zm12.035 2.672l-4.95 4.95-2.12-2.122-1.414 1.414 3.535 3.535 6.363-6.363-1.414-1.414z" />
          </svg>
        </div>
        <div className="col-10 text d-flex align-items-center">
          <h5>Udemy Courses</h5>
        </div>
      </div>
      <div className="btnBox my-3">
        {udemy.length>0 &&
          udemy.map((item,index) => {
            return (
              <button key={randomKey()} className="btn btn-outline-primary btn-sm">{item}</button>
            );
          })}
      </div>

      <div className="row subtitle">
        <div className="col-1 mr-3  icon">
          <svg
            fill="red"
            opacity="1.0"
            baseProfile="full"
            width="24"
            height="24"
            viewBox="0 0 24.00 24.00"
          >
            <path d="M10 16.498v-9l6 4.5m4-7.6c-.602-.197-4.3-.4-8.002-.4-3.699 0-7.398.187-8 .384-1.564.516-2 4.02-2 7.616s.436 7.1 2 7.616c.602.197 4.301.384 8 .384 3.702 0 7.4-.187 8.002-.384 1.562-.516 1.988-4.02 1.988-7.616s-.426-7.084-1.988-7.6z" />
          </svg>
        </div>
        <div className="col-10 text d-flex align-items-center">
          <h5>Youtube Courses</h5>
        </div>
      </div>
      <div className="btnBox my-3">
        {youtube.map((item) => {
          return (
            <h5  key={randomKey()}>
              <span className="badge badge-secondary badge-pill  badgeP ml-1">
                {item}
              </span>
            </h5>
          );
        })}
      </div>

      <div className="row subtitle">
        <div className="col-1 mr-3  icon">
          <svg
            fill="dodgerblue"
            opacity="1.0"
            baseProfile="full"
            width="24"
            height="24"
            viewBox="0 0 24.00 24.00"
          >
            <path d="M16.36 13.998c.083-.657.14-1.32.14-2 0-.679-.057-1.343-.14-2h3.377c.166.64.263 1.308.263 2s-.097 1.36-.263 2m-5.144 5.561a15.85 15.85 0 0 0 1.379-3.561h2.949a8.021 8.021 0 0 1-4.328 3.561zm-.254-5.561h-4.68a13.883 13.883 0 0 1-.16-2c0-.681.066-1.345.16-2h4.68c.095.655.16 1.319.16 2s-.065 1.345-.16 2zm-2.341 5.964a13.958 13.958 0 0 1-1.91-3.964h3.822a13.94 13.94 0 0 1-1.912 3.964zM8.026 7.998h-2.95a8 8 0 0 1 4.329-3.563 15.828 15.828 0 0 0-1.38 3.563zm-2.95 8h2.95c.323 1.251.779 2.45 1.379 3.563a8 8 0 0 1-4.328-3.563zm-.815-2a7.992 7.992 0 0 1-.261-2c0-.692.096-1.36.26-2h3.378a16.09 16.09 0 0 0-.138 2c0 .68.056 1.343.138 2m4.36-9.964a13.94 13.94 0 0 1 1.912 3.964h-3.821a13.96 13.96 0 0 1 1.909-3.964zm6.923 3.964h-2.95a15.85 15.85 0 0 0-1.378-3.56 8.021 8.021 0 0 1 4.328 3.56zm-6.927-6C6.468 1.998 2 6.475 2 11.998s4.468 10 9.994 10c5.527 0 10.006-4.477 10.006-10s-4.48-10-10.006-10z" />
          </svg>
        </div>
        <div className="col-10 text d-flex align-items-center">
          <h5>Web Courses</h5>
        </div>
      </div>
      <div className="btnBox my-3">
        {web.map((item) => {
          return (
            <h5  key={randomKey()}>
              <button className="btn btn-outline-primary btn-sm ml-1">
                {item}
              </button>
            </h5>
          );
        })}
      </div>
    </div>
  );
};

export default CourseCard;
