import "./ProjectCard.css";
import { useState } from "react";
import bekend1 from "../../../../../images/bekend1.png";
import bekend2 from "../../../../../images/bekend2.png";
import bekend3 from "../../../../../images/bekend3.png";
import bekend4 from "../../../../../images/bekend4.png";
import bekend5 from "../../../../../images/bekend5.png";
import bekend6 from "../../../../../images/bekend6.png";
import bekend7 from "../../../../../images/bekend7.png";
import web1 from "../../../../../images/web1.png";
import web2 from "../../../../../images/web2.png";
import web3 from "../../../../../images/web3.png";
import web4 from "../../../../../images/web4.png";
import web5 from "../../../../../images/web5.png";
import web6 from "../../../../../images/web6.png";

import flutter_app1_img1 from "../../../../../images/flutter_app1_img1.png";
import flutter_app2_img1 from "../../../../../images/flutter_app2_img1.png";
import flutter_app3_img1 from "../../../../../images/flutter_app3_img1.png";
import flutter_app4_img1 from "../../../../../images/flutter_app4_img1.png";
import flutter_app5_img1 from "../../../../../images/flutter_app5_img1.png";
import flutter_app6_img1 from "../../../../../images/flutter_app6_img1.png";

function randomKey() {
  return Math.floor(Math.random() * Date.now());
}
const ProjectCard = () => {
  const [courseList, setCourseList] = useState({
    bekend: [
      {
        image: bekend7,
        title: "Title",
      },
      {
        image: bekend1,
        title: "Title",
      },
      {
        image: bekend2,
        title: "Title",
      },
      {
        image: bekend3,
        title: "Title",
      },
      {
        image: bekend4,
        title: "Title",
      },
      {
        image: bekend5,
        title: "Title",
      },
      {
        image: bekend6,
        title: "Title",
      },
    ],
    webiste: [
      {
        image: web1,
        title: "Title",
      },
      {
        image: web2,
        title: "Title",
      },
      {
        image: web3,
        title: "Title",
      },
      {
        image: web4,
        title: "Title",
      },
      {
        image: web5,
        title: "Title",
      },
      {
        image: web6,
        title: "Title",
      },
    ],
    flutter: [
      {
        image: flutter_app1_img1,
        title: "Title",
      },
      {
        image: flutter_app2_img1,
        title: "Title",
      },
      {
        image: flutter_app3_img1,
        title: "Title",
      },
      {
        image: flutter_app4_img1,
        title: "Title",
      },
      {
        image: flutter_app5_img1,
        title: "Title",
      },
      {
        image: flutter_app6_img1,
        title: "Title",
      },
    ],
  });
  return (
    <div className="projectCard p-4">

      
      <div className="titleBox my-3">
        <h2>Bekend Projects</h2>
      </div>
      <div className="innerBox">
      {courseList.bekend.map((item, index) => {
        return <InnerCard key={randomKey()} {...item} />;
      })}
      </div>
    
    
    


      <div className="titleBox my-3">
        <h2>Bekend Projects</h2>
      </div>
      <div className="innerBox">
      {courseList.webiste.map((item, index) => {
        return <InnerCard key={randomKey()} {...item} />;
      })}
      </div>

      <div className="titleBox my-3">
        <h2>Flutter Projects</h2>
      </div>
      <div className="innerBox">
      {courseList.flutter.map((item, index) => {
        return <InnerCard key={randomKey()} {...item} />;
      })}
      </div>
    
    
    

    </div>
  );
};

const InnerCard = ({ title, image }) => {
  return (
    <a href={image} >
    <div className="card p-3">
      <img src={image} className="card-img-top"/>
      <div className="card-body">
        <div className="card-title">{title}</div>
      </div>
    </div>
     </a>
  );
};

export default ProjectCard;
